/**
 * Application configuration
 */
export const schedulerProConfig = (startDate2, endDate2) => {
    const utcDate = new Date(startDate2); // Exemplo de data UTC
    const year = utcDate.getUTCFullYear();
    const month = utcDate.getUTCMonth() + 1; // Adicionar 1 porque os meses começam em 0
    const day = utcDate.getUTCDate();
    return {
        startDate: year + "/" + month + "/" + day,
        endDate: endDate2,
        viewPreset: ({
            base: 'hourAndDay',                // Extends 'hourAndDay' view preset provided by PresetManager. You can pick out any of PresetManager's view presets: PresetManager.records
            search: true,
            timeResolution: {                  // Override time resolution
                unit: 'hour',
                increment: 1                  // Make it increment every 15 mins
            },

            headers: [

                // Override headers
                {
                    unit: 'day',
                    dateFormat: 'DD.MM.YYYY'   // Use different date format for top header 01.10.2020
                },
                {
                    unit: 'hour',
                    dateFormat: 'HH:SS'
                }
            ]
        }),
        features: {
            percentBar: true,
            search: true,
            filterBar: true,

            eventDrag: {
                constrainDragToResource: true
            },
            taskEdit: {
           
                items: {
                    generalTab: {
                        title: 'Dados',
                        showTitle:false,
                        items: {

                            nameField:
                            {

                                label: 'Nome',

                            },
                            startDateField:
                            {

                                label: 'Início',
                                readOnly: false,
                                format: 'DDMMYY'

                            },
                            endDateField:
                            {

                                label: 'Fim',
                                readOnly: false

                            },
                            // Remove "Duration" and "% Complete" fields in the "General" tab
                            durationField: false,
                            percentDoneField: false,
                            effortField: false,
                            resourcesField: false,
                        }
                    },
                    // Remove all tabs except the "General" tab
                    notesTab: false,
                    predecessorsTab: false,
                    successorsTab: false,
                    advancedTab: false
                },
                editorConfig: {
                    title : 'Editar Programação',
           
                    bbar: {
                        // Remove delete button
                        items: {
                            saveButton: {
                                text: 'Salvar', // Altere o texto do botão, se necessário
                                handler: function (button) {
                                    const editor = button.up('eventeditor');
                                    const eventRecord = editor.eventRecord;
                                    const name = editor.nameField.getValue();
                                    const startDate = editor.startDateField.getValue();
                                    const endDate = editor.endDateField.getValue();

                                }
                            },
                            deleteButton: {

                                text: 'Excluir'
                            },
                            cancelButton: {

                                text: 'Cancelar'
                            }
                        }
                    }
                }
            }
        },


        columns: [
            {
                collapseMode: 'showFirst',
                collapsed: true,
                collapsible: true,
                type: 'resourceInfo',
                text: 'Motorista',
                field: 'name',
                showEventCount: false,

                showRole: 'base',
                readOnly: true,
                exportable: true,
                searchable: true,
                sortable: true,
                filterable: {
                    filterField: {
                        type: 'text',
                        value: '',


                    }
                }
            }

        ]
    }
};
