import React, { useRef, useState, useEffect } from 'react';
import { BryntumSchedulerPro, BryntumProjectModel, ZoomSlider, ZoomCombo } from '@bryntum/schedulerpro-react';
import {
    CButton,
    CForm,
    CInputGroup,
    CFormInput,
    CInputGroupText,

} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilUser, cilDate, cilCalendar } from '@coreui/icons'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { format } from 'date-fns';
import { registerLocale, setDefaultLocale } from "react-datepicker";
import pt from 'date-fns/locale/pt-BR';
import { schedulerProConfig } from './AppConfig';
import './App.scss';

function GanttExample() {
    registerLocale('pt-BR', pt);
    const schedulerpro = useRef();
    const project = useRef();
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    const [resources, setResources] = useState([]);
    const [events, setEvents] = useState([]);
    const [assignments, setAssignments] = useState([]);
    const [loading, setLoading] = useState(true);
    let [filterData, setFilterData] = useState({
        locationGroupId: '', // Filtro do Grupo de Localização
        gpId: '', // Filtro do GPID
        nickName: '', // Filtro do Nome da Escala

    });
    const handleLocationGroupIdChange = (e) => {
        setFilterData((prevFilterData) => ({
            ...prevFilterData, locationGroupId: e.target.value
        }));
    };

    const handleGpIdChange = (e) => {
        setFilterData((prevFilterData) => ({
            ...prevFilterData, gpId: e.target.value
        }));
    };
    const handleNickNameChange = (e) => {
        setFilterData((prevFilterData) => ({
            ...prevFilterData, nickName: e.target.value
        }));
    };

    useEffect(() => {


        // Restante do código permanece o mesmo
    }, []); // Dependência vazia para executar apenas uma vez

    // Função para buscar os dados da API
    async function fetchData(startDate, endDate) {
        let apiUrl = `https://apicarga.azurewebsites.net/Journey/GetDriversJourney?startDate=${format(startDate, 'yyyy-MM-dd')}&endDate=${format(endDate, 'yyyy-MM-dd')}`;
        if (filterData.locationGroupId) {
            apiUrl += `&locationGroupId=${filterData.locationGroupId}`;
        }
        if (filterData.gpId) {
            apiUrl += `&gpId=${filterData.gpId}`;
        }
        if (filterData.nickName) {
            apiUrl += `&nickName=${filterData.nickName}`;
        }

        try {
            const response = await fetch(apiUrl);
            if (!response.ok) {
                throw new Error(`Erro na solicitação à API: ${response.status}`);
            }

            const data = await response.json();

            const newResources = [];
            const newEvents = [];
            const newAssignments = [];

            data.forEach((driver, index) => {
                newResources.push({
                    id: index + 1,
                    name: driver.nickName,
                    iconCls: 'b-fa b-fa-user',
                    base: driver.baseCode,
                    image: false,
                });

                driver.driverSchedules.forEach((schedule, eventIndex) => {
                    if (schedule.textProg && schedule.textTime) {
                        var startDateStr = `${schedule.journeyDate.substring(0, 10)}T${schedule.textTime.split(' ')[0]}`;
                        console.log(startDateStr);
                        var startTime = new Date(startDateStr);
                        var endTimeStr = `${schedule.journeyDate.substring(0, 10)}T${schedule.textTime.split(' ')[1]}`;
                        var endTime = new Date(endTimeStr);

                        if (endTime < startTime) {
                            endTime.setDate(endTime.getDate() + 1);
                        }

                        newEvents.push({
                            color: "red",
                            id: newEvents.length + 1,
                            name: schedule.textProg,
                            startDate: startDateStr,
                            endDate: endTimeStr,

                            iconCls: 'b-fa b-fa-arrow-truck',
                        });

                        newAssignments.push({
                            id: newAssignments.length + 1,
                            resourceId: index + 1,
                            eventId: newEvents.length,
                        });
                    }
                });
            });

            setResources(newResources);
            setEvents(newEvents);
            setAssignments(newAssignments);
            setLoading(false);
            schedulerpro.zoomTo(10)
        } catch (error) {
            console.error('Erro ao buscar os dados da API:', error);
            setLoading(false);
        }
    }

    const [locationGroupOptions, setLocationGroupOptions] = useState([]);

    // Função para buscar os dados dos grupos de localização na API
    async function fetchLocationGroupOptions() {
        try {
            const response = await fetch('https://apicargahomolog.azurewebsites.net/LocationGroup'); // Substitua 'API_ENDPOINT' pela URL da sua API
            if (!response.ok) {
                throw new Error(`Erro na solicitação à API: ${response.status}`);
            }

            const data = await response.json();

            // Mapeie os dados da API para o formato de options (value e label)
            const options = data.map((group) => ({
                value: group.id, // Substitua 'id' pelo campo apropriado da API
                label: group.code, // Substitua 'name' pelo campo apropriado da API
            }));

            setLocationGroupOptions(options);
        } catch (error) {
            console.error('Erro ao buscar os dados da API:', error);
        }
    }

    // Chamando a função para buscar os dados dos grupos de localização quando o componente é montado
    useEffect(() => {
        fetchLocationGroupOptions();
    }, []);


    return (
        <>
            <CForm>
                <CInputGroup className="mb-3">
                    <DatePicker showIcon={true} dateFormat="dd/MM/yyyy" locale="pt-BR" selected={startDate} onChange={(date) => setStartDate(date)} />
                    <DatePicker showIcon={true} dateFormat="dd/MM/yyyy" locale="pt-BR" selected={endDate} onChange={(date) => setEndDate(date)} />
                    <CFormInput
                        placeholder="Nome da Escala"
                        onChange={handleNickNameChange}
                    />
                    <CFormInput
                        placeholder="GPID"
                        onChange={handleGpIdChange}
                    />


                    <select
                        value={filterData.locationGroupId}
                        onChange={handleLocationGroupIdChange}
                        className="form-select"
                    >
                        <option value="">Selecione o Grupo de Localização</option>
                        {locationGroupOptions.map((option) => (
                            <option key={option.value} value={option.value}>
                                {option.label}
                            </option>
                        ))}
                    </select>
                    <CButton color="primary" size="sm" onClick={() => fetchData(startDate, endDate)}>
                        Filtrar
                    </CButton>
                </CInputGroup>

            </CForm>

            {loading ? (
                <></>
            ) : (
                <>

                    <BryntumSchedulerPro
                        height={window.innerHeight - 60}
                        width={window.innerWidth - 80}
                        ref={schedulerpro}
                        project={project}
                        events={events}
                        resources={resources}
                        assignments={assignments}

                        {...schedulerProConfig(startDate, endDate)}
                    >
                    </BryntumSchedulerPro>
                </>
            )}
        </>
    );
}

export default GanttExample;

