import React, { Suspense } from 'react'
import { Navigate, Route, Routes } from 'react-router-dom'
import { CContainer, CSpinner } from '@coreui/react'
import { token } from '../config/globals'
import routes from '../routes'
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const handleLogin = (accessCode) => {



  fetch('https://apicargodev.azurewebsites.net/api/gettoken', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },

  })
    .then(response => response.json())
    .then(data => {


      // Trate a resposta do servidor conforme necessário
      if (data.authenticated) {

        token = data.accessToken;
        localStorage.setItem('token', token);
        toast.success('Login realizado com sucesso!',);
        window.location.href = "/#/";
      }
      else {
        toast.error('Erro ao fazer login:');
      }



    })
    .catch(error => {

      toast.error('Erro ao fazer login:');
    });
};

const AppContent = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const accessCode = urlParams.get('accessCode');
  if (token != '') {
    return (
    
        <Suspense fallback={<CSpinner color="primary" />}>
          <Routes>
            {routes.map((route, idx) => {
              return (
                route.element && (
                  <Route
                    key={idx}
                    path={route.path}
                    exact={route.exact}
                    name={route.name}
                    element={<route.element />}
                  />
                )
              )
            })}
            <Route path="/" element={<Navigate to="dashboard" replace />} />
          </Routes>
        </Suspense>
  
    );
  } else if (accessCode != '') {


    return (
      <CContainer lg>
        <Suspense fallback={<CSpinner color="primary" />}>
          <Routes>
            <Route path="*" element={<Navigate to="login" replace />} />
          </Routes>
        </Suspense>
      </CContainer>
    )
  }

  else {
    return (
      <CContainer lg>
        <Suspense fallback={<CSpinner color="primary" />}>
          <Routes>
            <Route path="*" element={<Navigate to="login" replace />} />
          </Routes>
        </Suspense>
      </CContainer>
    )
  }
}

export default React.memo(AppContent)
