import React, { useEffect, useState } from 'react'
import { AppContent, AppSidebar, AppFooter, AppHeader } from '../../components/index'
import { useLocation } from 'react-router-dom';
import {token} from '../../config/globals'

const Activity = (link) => {
  const [iframeSrc, setIframeSrc] = useState(0);
  const location = useLocation();


  useEffect(() => {
    var urlDoNavegador = window.location.href;
    var url = urlDoNavegador.split('/');
    var iframeSrc = `https://cargodev.sigla.app/activity?accessCode=${token}`;
    setIframeSrc(iframeSrc);
  }, []);



  return (
  <div>
    <iframe title="Pepsico" src={iframeSrc} width="100%" height={window.innerHeight} />
  </div >);
}

export default Activity
