

export var token = "";
export var userName = "";
export var gpId = "";
export var expiration = "";
// Inicialmente, verifique se há um token no Local Storage e defina a variável global a partir dele, se existir
if (localStorage.getItem('token')) {
    token = localStorage.getItem('token');
    expiration = localStorage.getItem('expiration');
    userName = localStorage.getItem('userName');
    if (!localStorage.getItem('gpId')) {
        try {



            // Construa a base da URL
            const baseUrl = 'https://apicarga.azurewebsites.net/api/getuser';

            // Codifique o token para garantir que caracteres especiais sejam tratados corretamente
            const encodedToken = encodeURIComponent(token);

            // Construa a URL completa
            var apiUrl = `${baseUrl}?token=Bearer ${encodedToken}`;
             apiUrl = `${apiUrl.replace(/ /g, ' ')}`;
            // Faça a solicitação GET
            const response = await fetch(apiUrl);


            if (!response.ok) {
                throw new Error(`Erro na solicitação à API: ${response.status}`);
            }

            const data = await response.json();
            console.log(data);
            localStorage.setItem('gpId', data.gpId);

        } catch (error) {
            console.error('Erro ao buscar os dados da API:', error);
        }
    }
    else {
        gpId = localStorage.getItem('gpId');
        console.log(gpId + "teste");
    }
}
else {
    token = "";
}



