    console.log(location.pathname + "-");

    useEffect(() => {
        var urlDoNavegador = window.location.href;
        var url = urlDoNavegador.split('/');
        var iframeSrc = `https://cargodev.sigla.app/location-group?accessCode=${token}`;

        console.log(iframeSrc);
        setIframeSrc(iframeSrc);
    }, []);

    const extrairCaminho = (url) => {
        const urlObj = new URL(url);
        return urlObj.pathname; // Obtém o caminho (path) da URL
    };

    return (<div>
        

        <iframe title="Pepsico" src={iframeSrc} width="100%" height={window.innerHeight} />



    </div >);
